body {
  background-color: #f1e0c7;
}

.hide {
  display: none;
}

.breed-select {
  width: 300px;
  height: 50px;
  color: #4f4b4b;
  background-color: #faebd7;
  border-width: 2px;
  border-color: #7c7878;
  border-radius: 10px;
  font-size: 28px;
}

.cat-info {
  margin: 20px;
  display: flex;
}

.catImg {
  width: 400px;
  padding-right: 20px;
}

.information {
  width: 50%;
}

.text {
  font-size: 16px;
  font-weight: normal;
}

.loader.hide {
  display: none;
}

.loader {
  width: 10px;
  height: 10px;
  color: (79, 75, 75);
  box-sizing: border-box;
  border-radius: 50%;
  margin-top: 15px;
  margin-left: 30px;
  animation: shadowRolling 2s linear infinite;
  display: block;
  position: relative;
  left: -100px;
}

@keyframes shadowRolling {
  0% {
    box-shadow: 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0);
  }

  12% {
    box-shadow: 100px 0 #fff, 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0);
  }

  25% {
    box-shadow: 110px 0 #fff, 100px 0 #fff, 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0);
  }

  36% {
    box-shadow: 120px 0 #fff, 110px 0 #fff, 100px 0 #fff, 0 0 rgba(255, 255, 255, 0);
  }

  50% {
    box-shadow: 130px 0 #fff, 120px 0 #fff, 110px 0 #fff, 100px 0 #fff;
  }

  62% {
    box-shadow: 200px 0 rgba(255, 255, 255, 0), 130px 0 #fff, 120px 0 #fff, 110px 0 #fff;
  }

  75% {
    box-shadow: 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 130px 0 #fff, 120px 0 #fff;
  }

  87% {
    box-shadow: 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 130px 0 #fff;
  }

  100% {
    box-shadow: 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0);
  }
}

